body {
  margin: 0;
  padding: 0;
  font-family: 'Circe', 'Arial', sans-serif;
  font-weight: 400;
  background-color: #fcfcfd;
}
.img-desktop {
  display: block;
}
.img-mobile {
  display: none;
}
img {
  max-width: 100%;
  height: auto;
}
p {
  color: #747474;
}
a {
  text-decoration: none;
  color: #2f2f2f;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
h1,
h2,
h3 {
  color: #2f2f2f;
}
h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}
button,
input {
  font-family: 'Circe', 'Arial', sans-serif;
}
.noscroll {
  overflow: hidden !important;
  height: 100% !important;
}
.overlay {
  display: none;
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 1;
  visibility: visible;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.active.overlay {
  display: block;
}
.huge-img {
  display: none;
  z-index: 200;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 600px;
  height: auto;
}
.huge-img img {
  width: 100%;
  border-radius: 14px;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  max-width: 1625px;
  width: 84.6%;
  margin-left: auto;
  margin-right: auto;
}
.container-out-right {
  width: 92.187vw;
  margin-left: auto;
}
.double-main-wrapper {
  display: flex;
  justify-content: space-between;
}
.double-column-wrapper {
  width: 62vw;
  padding-top: 1.0416vw;
}
.double-aside-wrapper {
  width: 19.896vw;
  padding-top: 1.0416vw;
}
.hidden-mobile-block {
  display: none;
}
.btn {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12.656vw;
  height: 2.8645vw;
  text-align: center;
  text-transform: uppercase;
  font-size: 0.9375vw;
  font-weight: 400;
  letter-spacing: 0.09px;
  color: #ffffff;
  background-color: #14b250;
  cursor: pointer;
  border: none;
  border-radius: 19px;
}
.btn:hover,
.btn:focus {
  color: rgba(255, 255, 255, 0.7);
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
}
.btn:active {
  color: rgba(255, 255, 255, 0.5);
}
.btn:active {
  padding: 0;
}
.warning__input:-moz-ui-invalid,
.phone-mask:-moz-ui-invalid {
  box-shadow: none;
}
