}
.go-top-btn {
  position: fixed;
  z-index: 50;
  transition-duration: 1s;
  opacity: 0;
  left: 50%;
  transform: translateX(43.49vw);
  bottom: 5.208vw;
  width: 5.365vw;
  height: 5.365vw;
  padding: 0;
  border: none;
  border-radius: 20px;
  background-color: #ffffff;
  box-shadow: 0 8px 68px rgba(15, 13, 47, 0.15);
  cursor: pointer;
}
.go-top-btn.active {
  opacity: 1;
}
.go-top-btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2.6041vw;
  height: 1.1979vw;
  background-image: url('../img/svg/arrow-brown.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (max-width: 755px) {
  .img-desktop {
    display: none;
  }
  .img-mobile {
    display: block;
  }
  .container,
  .container-out-right {
    width: auto;
    padding-left: 4.666vw;
    padding-right: 4.666vw;
  }
  .double-column-wrapper {
    width: 90.4vw;
  }
  .double-aside-wrapper {
    display: none;
  }
  .hidden-desktop-block {
    display: none;
  }
  .hidden-mobile-block {
    display: block;
  }
  .btn {
    max-width: none;
    max-height: none;
    width: 36vw;
    height: 8.799vw;
    font-size: 3.599vw;
    border-radius: 38px;
  }
  .go-top-btn {
    display: none!important;
  }
}
@media (min-width: 1921px) {
  .container-out-right {
    width: 1770px;
    margin-right: auto;
    transform: translateX(75px);
  }
  .double-column-wrapper {
    width: 1190px;
    padding-top: 20px;
  }
  .double-aside-wrapper {
    width: 382px;
    padding-top: 20px;
  }
  .btn {
    width: 243px;
    height: 55px;
    font-size: 18px;
  }
  .go-top-btn {
    transform: translateX(835px);
    bottom: 100px;
    width: 103px;
    height: 103px;
  }
  .go-top-btn::before {
    width: 50px;
    height: 23px;
  }
}
/*header*/
.header {
  position: relative;
  max-height: 175px;
  height: 9.115vw;
  letter-spacing: 0.2px;
  color: #2f2f2f;
  background-color: #fff;
}
.header__mobile {
  display: none;
}
.header__mobile-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__desktop {
  padding-top: 1.5625vw;
}
.header__desktop-top {
  display: block;
  padding-bottom: 1.0416vw;
}
.header__desktop-bottom {
  background-color: #f8f8f8;
}
.header__consultation-phone {
  display: none;
}
.header__consultation-phone--link {
  font-size: 6vw;
  font-weight: 700;
  color: #2f2f2f;
  text-decoration: none;
}
.header__callback {
  display: none;
}
/*styles for index page*/
.index-bg {
  display: none;
  position: absolute;
  z-index: -1;
  max-height: 1000px;
  min-height: 49.5vw;
  width: 100%;
  background-color: #00497a;
}
.index-bg__img {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.main.index .index-bg {
  display: block;
}
.header.index {
  position: absolute;
  z-index: 15;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  background-color: transparent;
}
.header.index .header__desktop-bottom {
  background-color: transparent;
}
.header.index,
.header.index p,
.header.index .nav-info__link,
.header.index .main-nav__link,
.header.index .nav-section__link,
.header.index .logo,
.header.index .logo__text--strong {
  color: #ffffff;
}
.header.index .logo__text,
.header.index .logo__text--strong {
  font-weight: 400;
}
.header.index .menu-toggle span {
  transition: 0.3s 0.4s;
}
.header.index .menu-toggle span,
.header.index .menu-toggle::before,
.header.index .menu-toggle::after {
  background-color: #ffffff;
}
.header.index .menu-toggle.active span {
  background-color: transparent;
}
.header.index .nav-section__item--services {
  display: none;
}
.header.index .logo__text::before {
  background-image: url("../img/svg/logo-nd-white.svg");
}
.header.index .header__search-link::before {
  background-image: url("../img/svg/search-white.svg");
}
.header.index .nav-info__item--search .nav-info__link::before {
  background-image: url('../img/svg/search-white.svg');
}
.header.index .nav-section__link .nav-section__icon:first-of-type {
  display: none;
}
.header.index .nav-section__link .nav-section__icon:last-of-type {
  display: block;
}
.search-container {
  display: none;
}
@media (max-width: 755px) {
  .header {
    z-index: 20;
    position: fixed;
    width: 100%;
    max-height: none;
    height: 16.8vw;
